import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { SQLite } from '@ionic-native/sqlite';
import { Keyboard } from '@ionic-native/keyboard';

import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';

import { DrawPage } from '../pages/draw/draw';

import { SpeedexamPage } from '../pages/speedexam/speedexam';
import { PlusnotesPage } from '../pages/plusnotes/plusnotes';
import { LandingPage } from '../pages/landing/landing';
import { ProgressData } from '../providers/progressdata';
import { SinglegalleryPage } from '../pages/singlegallery/singlegallery';
import { SinglenotesPage } from '../pages/singlenotes/singlenotes';
import { IonicStorageModule } from '@ionic/storage';
import { CanvasDrawComponent } from '../components/canvas-draw/canvas-draw';
import { CanvasSinglegalleryComponent } from '../components/canvas-singlegallery/canvas-singlegallery';


@NgModule({
  declarations: [
    MyApp,
    HomePage,
    DrawPage,

    SpeedexamPage,
    PlusnotesPage,
    LandingPage,
    SinglegalleryPage,
    SinglenotesPage,
    CanvasDrawComponent,
    CanvasSinglegalleryComponent
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/singlenotes/singlenotes.module#SinglenotesPageModule', name: 'SinglenotesPage', segment: 'singlenotes', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/singlegallery/singlegallery.module#SinglegalleryPageModule', name: 'SinglegalleryPage', segment: 'singlegallery', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/landing/landing.module#LandingPageModule', name: 'LandingPage', segment: 'landing', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/plusnotes/plusnotes.module#PlusnotesPageModule', name: 'PlusnotesPage', segment: 'plusnotes', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/speedexam/speedexam.module#SpeedexamPageModule', name: 'SpeedexamPage', segment: 'speedexam', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/draw/draw.module#DrawPageModule', name: 'DrawPage', segment: 'draw', priority: 'low', defaultHistory: [] }
  ]
}),
    IonicStorageModule.forRoot()
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    DrawPage,

    SpeedexamPage,
    PlusnotesPage,
    LandingPage,
    SinglegalleryPage,
    SinglenotesPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    ProgressData ,
    SQLite,
    Keyboard
  ]
})
export class AppModule {
  
}



